import { useState, useEffect } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Table,
  Header,
  HeaderRow,
  HeaderCell,
  Body,
  Row,
  Cell,
} from "@table-library/react-table-library/table";
import { useTheme } from "@table-library/react-table-library/theme";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";
import { Link } from "react-router-dom";

const THEME = {
  HeaderRow: `  
    font-weight: bold;
    font-size: 24px;
 
    background-color: #eaf5fd;
  `,
  Row: `
    font-size: 16px;
    
    &:hover {
      color: #212529;
      cursor: default;
    }
  `,
};

function Currency() {
  let [curritem, setCurritem] = useState([]);
  let [league, setLeague] = useState("Scourge");
  const [search, setSearch] = useState("");
  const theme = useTheme(THEME);

  useEffect(() => {
    async function fetchData() {
      const result = await axios.get(`/api/getCurrency?League=${league}`);
      //console.log("Result from api:");
      console.log(result);
      setCurritem([...result.data]);
    }
    fetchData();
  }, [league]);

  const data = {
    nodes: curritem.filter((item) =>
      item.Name.toLowerCase().includes(search.toLowerCase())
    ),
  };
  return (
    <>
      <div>Select ongoing League</div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <DropdownButton id="dropdown-basic-button" title={league}>
          <Dropdown.Item
            onClick={(e) => {
              setLeague(e.target.textContent);
            }}
          >
            Scourge
          </Dropdown.Item>
          <Dropdown.Item
            onClick={(e) => {
              setLeague(e.target.textContent);
            }}
          >
            Hardcore Scourge
          </Dropdown.Item>
          <Dropdown.Item
            onClick={(e) => {
              setLeague(e.target.textContent);
            }}
          >
            Standard
          </Dropdown.Item>
          <Dropdown.Item
            onClick={(e) => {
              setLeague(e.target.textContent);
            }}
          >
            Hardcore
          </Dropdown.Item>
        </DropdownButton>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <label htmlFor="search">
          Search by Name: <br></br>
          <input
            id="search"
            type="text"
            onChange={(event) => {
              setSearch(event.target.value);
            }}
          />
        </label>
      </div>
      <>
        <Table data={data} theme={theme} layout={{ custom: true }}>
          {(tableList) => (
            <>
              <Header>
                <HeaderRow>
                  <HeaderCell style={{ width: "100px" }}>Item</HeaderCell>
                  <HeaderCell style={{ width: "100px" }}>Price</HeaderCell>
                </HeaderRow>
              </Header>
              <Body>
                {tableList.map((cur, id) => (
                  <Row key={id} item={cur} style={{ width: "200px" }}>
                    <Cell style={{ width: "100px" }}>
                      {
                        <img
                          src={cur.Image}
                          alt={"Picture of " + cur.Name}
                        ></img>
                      }{" "}
                      <Link
                        to={{
                          pathname: `/Currency/${cur.Name}`,
                          state: {
                            CurName: cur.Name,
                            Id: cur.Id,
                            League: league,
                          },
                        }}
                      >
                        {cur.Name}
                      </Link>
                    </Cell>
                    <Cell style={{ width: "100px" }}>{cur.Price}</Cell>
                  </Row>
                ))}
              </Body>
            </>
          )}
        </Table>
      </>
    </>
  );
}

export default Currency;
