import { useState, useEffect } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
} from "recharts";
import { useLocation } from "react-router-dom";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";
import Button from "react-bootstrap/button";

function CurrencyDetail({ match }) {
  const location = useLocation();
  let [item, setItem] = useState([]);
  let [predicteditem, setPredictedItem] = useState([]);
  let [league, setLeague] = useState([location.state.League]);
  let [pastleague, setPastLeague] = useState("Expedition");
  const [showPrediction, setShowPrediction] = useState(false);
  let itemLink = "";

  async function fetchPredictedPrice() {
    const result = await axios.get(
      `/api/getPredictCurrency?Name=${location.state.CurName}`
    );
    console.log(result);
    setPredictedItem([...result.data]);
  }

  async function fetchOldData(oldLeague) {
    setPastLeague(oldLeague);
    const result = await axios.get(
      `/api/getPastCurrency?League=${oldLeague}&Id=${location.state.Id}`
    );
    //console.log("Result from api:");
    console.log(result);
    setItem([...result.data]);
  }

  useEffect(() => {
    console.log("location:");
    console.log(location);
    async function fetchData() {
      const result = await axios.get(
        `/api/getCurrencyDetails?League=${league}&name=${match.params.id}&Id=${location.state.Id}`
      );
      console.log(result);
      setItem([...result.data]);
    }
    fetchData();
    if (league[0] === "Scourge") {
      setShowPrediction(true);
    } else setShowPrediction(false);
    itemLink = match.params.id.replace(/ /g, "_", function (x) {
      return `https://www.poewiki.net/wiki/${x}`;
    });
  }, [league, location, match.params.id]);

  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      return (
        <div className="custom-tooltip">
          <p className="label">{`Day: ${label}`}</p>
          <p className="price">{`Price: ${payload[0].value} Chaos`}</p>
        </div>
      );
    }

    return null;
  };

  const League = () => (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <h2>Predicted price</h2>
      </div>
      <Button variant="info" onClick={() => fetchPredictedPrice()}>
        Show predicted price
      </Button>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <LineChart
          width={1200}
          height={600}
          data={predicteditem}
          margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
        >
          <Line type="monotone" dataKey="Price" stroke="#8884d8" />
          <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
          <XAxis
            dataKey="Day"
            label={{
              value: "Days since League start",
              dy: 10,
            }}
          />
          <YAxis
            label={{
              value: "Price in Chaos",
              position: "insideTopLeft",
              dx: 75,
            }}
          />
          <Tooltip content={<CustomTooltip />} />
        </LineChart>
      </div>
    </>
  );

  return (
    <>
      <h3>Detailed overview for</h3>
      <h3>
        {" "}
        <a href={itemLink}>{match.params.id}</a>
      </h3>
      <div>Select ongoing League</div>
      <DropdownButton id="dropdown-basic-button" title={league}>
        <Dropdown.Item
          onClick={(e) => {
            setLeague([...[e.target.textContent]]);
          }}
        >
          Scourge
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            setLeague([...[e.target.textContent]]);
          }}
        >
          Hardcore Scourge
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            setLeague([...[e.target.textContent]]);
          }}
        >
          Standard
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            setLeague([...[e.target.textContent]]);
          }}
        >
          Hardcore
        </Dropdown.Item>
      </DropdownButton>
      <div>Browse past League data</div>
      <DropdownButton id="dropdown-basic-button" title={pastleague}>
        <Dropdown.Item
          onClick={(e) => {
            fetchOldData(e.target.textContent);
          }}
        >
          Expedition
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            fetchOldData(e.target.textContent);
          }}
        >
          Hardcore Expedition
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            fetchOldData(e.target.textContent);
          }}
        >
          Ultimatum
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            fetchOldData(e.target.textContent);
          }}
        >
          Hardcore Ultimatum
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            fetchOldData(e.target.textContent);
          }}
        >
          Ritual
        </Dropdown.Item>
        <Dropdown.Item
          onClick={(e) => {
            fetchOldData(e.target.textContent);
          }}
        >
          Hardcore Ritual
        </Dropdown.Item>
      </DropdownButton>

      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <h2>Real price</h2>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <LineChart
          width={1200}
          height={600}
          data={item}
          margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
        >
          <Line type="monotone" dataKey="History" stroke="#8884d8" />
          <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
          <XAxis dataKey="Dates" />
          <YAxis />
        </LineChart>
      </div>
      {showPrediction ? <League /> : null}
    </>
  );
}

export default CurrencyDetail;
